.loadingMain
{
    position: fixed;
    top:0; 
    width:100%;
    height:100vh;
    background-color: white;
    z-index:100000;
    align-items:center;
    justify-content: center;
    
}


.loadingImg
{
    width:350px;
    height:auto;
    z-index: 100000;
}

.zero{
    opacity: 1;
    align-items:center;
    justify-content: center;
}

::-webkit-scrollbar {
    display: none;
}