
header
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center ; 
	transition: 0.6s;
	padding: 40px 100px;
	z-index: 10000;
}

header.sticky
{
	padding: 5px 100px;
	background: white;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

header .logo
{
	position: relative;
	font-weight: 900;
	color: white !important;
	text-decoration: none;
	font-size: 2em;
	text-transform: uppercase;
	Letter-spacing: 2px;
	transition: 0.6s;
}



header ul
{
	position: relative;
	display: flex;
	justify-content: center ;
	align-items: center ;
	margin-top: 10px;
}

header ul li
{
	position: relative;
	list-style: none;
	
}

header ul li a
{
	position: relative;
	margin: 0 15px;
	text-decoration: none;
	color: white !important;
	letter-spacing: 2px;
	transition: 0.6s;
}

header.sticky ul li a
{
	position: relative;
	margin: 0 15px;
	text-decoration: none;
	color: black !important;
	letter-spacing: 2px;
	transition: 0.6s;
}

header.sticky .logo
{
	position: relative;
	font-weight: 900;
	color: black !important;
	text-decoration: none;
	font-size: 2em;
	text-transform: uppercase;
	Letter-spacing: 2px;
	transition: 0.6s;
}